import React from "react";
import MegaNavbar from "./Navbar";
import { useCallback } from "react";
import Particles from "react-particles";
import { loadFull } from "tsparticles";
import { Box, Heading, Image, VStack } from "@chakra-ui/react";
import particles from "./particles";
import style from "./Home.module.css";
import image from "../Componens/Images/profile.jpeg";
import Text from "./Text";
import { ReactTypingEffectDemo } from "./Typingeffect";
import styled from "styled-components";

const handleinit = async (main) => {
  await loadFull(main);
};
function Home() {
  const text1 = "Full Stack Web Developer";
  // const text2 = "I'm";
  const text3 = "Prashant Rathour";
  console.log(style);
  return (
    <>
      <DIV>
        {/* <MegaNavbar /> */}
        <Box className="main"  id="home">
          <Particles id="particles" options={particles} init={handleinit} />
          <Box className="boxleft">
            <Heading className={style.style}>
              <Text Text={text3} size={"80px"} width={"100%"} />

              <>
                <ReactTypingEffectDemo size={"50px"} />
              </>
              {/* <Text  Text={text2} /> */}
            </Heading>
          </Box>
          <Box className="rightbox">
            <Box>
              <Image className="image" src={image} alt="photo" />
            </Box>
          </Box>
        </Box>
        <Box width={"100px"}></Box>
      </DIV>
    </>
  );
}

export default Home;
const DIV = styled.div`
  .main {
    width: 100%;
    display: flex;
    height: 100vh;
    margin: auto;
    justify-content: center;
    gap: 50px;
    align-items: center;

  }
  .boxleft {
 
    width: 50%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    align-content: center;
  }
  .boxright {
    width: 50%;
    display: flex;
  flex-direction: column;
  padding: 40px;
  background-color: #fff;

  transition: box-shadow 0.5s;
  will-change: transform;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
      rgba(0, 0, 0, 0.3) 0px 30px 60px -30px,
      rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
  }
  .image {
    width: 400px;
    object-fit: cover;
    height: 400px;
    border-radius: 10px;
  
  }
`;
